import React from 'react';
import PropTypes from 'prop-types';
import get from 'lodash/get';
import { Modal, ModalHeader, ModalBody } from 'reactstrap';

import helpers from './../../helpers';
import PokeTypes from './../PokeTypes';
import './style.css';

class List extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      isShow: false
    };
  }

  toggle = () => {
    this.setState(prevState => ({
      isShow: !prevState.isShow
    }));
  };

  getPokeImage = data => {
    return get(data, 'detail.sprites.front_default', '');
  };

  render() {
    const props = this.props;
    const state = this.state;
    const img = this.getPokeImage(props.data);
    const name = helpers.titleCase(props.data.name);

    return(
      <div className="list-wrapper" onClick={this.toggle}>
        <div className="row">
          <div className="image-box">
            <img src={img} alt="POKEMON_IMAGE"/>
          </div>
        </div>
        <div className="row left">
          <h3>{`#${props.data.detail.id}`}</h3>
        </div>
        <div className="row left">
          <h2 className="text-pokoname">{name}</h2>
        </div>
        <div className="row">
          <PokeTypes types={props.data.detail.types} />
        </div>

        <Modal isOpen={state.isShow} toggle={this.toggle} className={this.props.className} size="lg">
          <ModalHeader toggle={this.toggle}>
            <div>
              <h1 className="text-pokoname">{name}</h1>
            </div>
          </ModalHeader>
          <ModalBody>
            <div className="row">
              <div className="col-xs-6">
                <div className="image-box image-modal">
                  <img src={img} alt="POKEMON_IMAGE"/>
                </div>
              </div>

              <div className="col-xs-6">
                <div className="row" style={{ marginBottom: '30px' }}>
                  <div className="col-xs-6">
                    <h2>Height</h2>
                    <h3>{props.data.detail.height}</h3>
                  </div>
                  <div className="col-xs-6">
                    <h2>Weight</h2>
                    <h3>{props.data.detail.weight}</h3>
                  </div>
                </div>

                <div className="row" style={{ marginBottom: '30px' }}>
                  <div className="col-xs-12">
                    <h2>Abilities</h2>
                  </div>
                  {props.data.detail.abilities.map((list, key) => {
                    return (
                      <div className="col-xs-6" key={key}>
                        <h3>{helpers.titleCase(list.ability.name)}</h3>
                      </div>
                    )
                  })}
                </div>

                <div className="row" style={{ marginBottom: '30px' }}>
                  <div className="col-xs-12" style={{ marginBottom: '10px' }}>
                    <h2>Types</h2>
                  </div>
                  <div className="col-xs-6">
                    <PokeTypes types={props.data.detail.types} />
                  </div>
                </div>
              </div>
            </div>
          </ModalBody>
        </Modal>
      </div>
    )
  }
}

List.propTypes = {
  data: PropTypes.object
};

export default List;