import React, { Component } from 'react';
import { Waypoint } from 'react-waypoint';
import ClipLoader from 'react-spinners/ClipLoader';
import Sticky from 'react-stickynode';
import { Button, ButtonGroup } from 'reactstrap';
import uniq from 'lodash/uniq';
import sortBy from 'lodash/sortBy';
import filter from 'lodash/filter';

import helpers from './helpers';
import { getPokemon } from './models/pokemon';
// import SearchBox from './components/SearchBox';
import Images from './assets';
import List from './components/List';

import './styles/grid.min.css';
import './styles/App.css';

class App extends Component {
  constructor(props) {
    super(props);

    this.state = {
      pureData: [],
      data: [],
      next: 'https://pokeapi.co/api/v2/pokemon/',
      filter: [],
      checked: []
    };
  }

  handleGetPokemon = () => {
    const state = this.state;

    getPokemon(state.next).then(res => {
      const newData = state.data.concat(res.results);
      const newFilter = sortBy(uniq(state.filter.concat(res.filter)));

      this.setState({
        data: newData,
        next: res.next,
        filter: newFilter
      });
    });
  };

  handleCheck = selected => {
    const state = this.state;
    const index = state.checked.indexOf(selected);

    if (index < 0) {
      state.checked.push(selected);
    } else {
      state.checked.splice(index, 1);
    }

    this.setState({ checked: [...state.checked] });
  };

  handleFilter = data => {
    const state = this.state;

    if (state.checked.length > 0) {
      const retData = [];

      data.map(obj => {
        const fill = filter(obj.filter, function(list) {
          return state.checked.includes(list);
        });
  
        if (fill.length > 0) {
          retData.push(obj);
        }
      });

      return retData;
    } else {
      return data;
    }
  };

  render() {
    const state = this.state;

    return (
      <div className="App">
        <div className="header-wrapper">
          <h1>Pok√©dex</h1>  
          <img className="pokeball" src={Images.pokeball} alt="pokeball"/>
        </div>
        
        <div className="main-wrapper">
          {/* <SearchBox /> */}
          <div className="row">
            <div className="col-xs-3 left">
              <Sticky>
                <div className="filter-box">
                  <h2 style={{ marginBottom: '20px' }}>Filter</h2>
                  <div className="filter-content">
                    <ButtonGroup vertical>
                      {state.filter.map((list, key) => {
                        return (
                          <Button onClick={() => this.handleCheck(list)} active={state.checked.includes(list)} key={key} className="left" outline color="secondary" size="lg">
                            {helpers.titleCase(list)}
                          </Button>
                        )
                      })}
                    </ButtonGroup>
                  </div>
                </div>
              </Sticky>
            </div>

            <div className="col-xs-9">
              <div className="row">
                {this.handleFilter(state.data).map((list, key) => {
                  return (
                    <div className="col-xs-12 col-sm-4" key={key}>
                      <List data={list} />
                    </div>
                  )
                })}
              </div>

              <Waypoint
                onEnter={this.handleGetPokemon}
              >
                <div style={{ marginBottom: '10px' }}>
                  <ClipLoader
                    sizeUnit={"px"}
                    size={30}
                    color={'rgb(254, 207, 51)'}
                    loading
                  />
                </div>
              </Waypoint>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default App;
