
import React from 'react';
import PropTypes from 'prop-types';

import helpers from './../../helpers';
import './style.css';

class PokeTypes extends React.Component {
  render() {
    const props = this.props;
    

    return (
      <div className="poketypes-wrapper left">
        {props.types && props.types.map((list, key) => {
          return (
            <div className="type-box" key={key} style={{ backgroundColor: helpers.convertStringToColor(list.type.name)}}>
              {helpers.titleCase(list.type.name)}
            </div>
          )
        })}
      </div>
    )
  }
}

PokeTypes.propTypes = {
  types: PropTypes.array
};

export default PokeTypes;