export const getPokemon = async url => {
  const res = await fetch(url);
  let data = await res.json();
  const results = data.results;
  let arrMapping = [];

  if (results.length > 0) {
    const promises = results.map(async list => {
      list.detail = await getPokemonDetail(list.url);
      const item = mappingFilter(list.detail.types);

      list.filter = item;
      arrMapping = arrMapping.concat(item);

      return list;
    });
    data.results = await Promise.all(promises);
  }

  data.filter = arrMapping;

  return data;
};

export const getPokemonDetail = async url => {
  const res = await fetch(url);
  const detail = await res.json();

  return detail;
};

const mappingFilter = types => {
  const arr = [];

  types.map(list => {
    arr.push(list.type.name);
  });

  return arr;
};